
        import {store, MERGED_BUNDLES_NAME} from 'bundleStore';
        
        let bundle;
        
        if ('EditSettingsBundle' !== MERGED_BUNDLES_NAME) {
            store['EditSettingsBundle'] = store['EditSettingsBundle'] || {};
            bundle = store['EditSettingsBundle'];
        } else {
            bundle = Object.keys(store).reduce((acc, key) => {
                return {
                    ...acc,
                    ...store[key]
                }
            }, {});
        }
                                     
        export default bundle;
    